
  <template>
  <div class="my-wrapper" :class="{'is-fit': fit,'is-split':split,'is-no-header':!header}">
    <div v-if="header" class="my-wrapper__header" :class="classes">
      <my-breadcrumb class="my-wrapper__breadcrumb"
                     v-if="breadcrumb && breadcrumb.length"
                     :data="breadcrumb"></my-breadcrumb>
      <my-float v-if="$slots.title || $slots.actions || title"
                class="my-wrapper__ft">
        <my-float-item float="left"
                       class="my-wrapper__title">
          <span v-if="back" @click="handleBack" class="my-wrapper__back"><i class="el-icon-back"></i></span>
          <slot name="title">{{title}}</slot>
        </my-float-item>
        <my-float-item float="left" class="my-wrapper__sub-title">
          <slot name="subTitle">{{subTitle}}</slot>
        </my-float-item>
        <my-float-item v-if="$slots.actions"
                       class="my-wrapper__actions"
                       float="right">
          <slot name="actions"></slot>
        </my-float-item>
      </my-float>

      <div v-if="$slots.extra || extra" class="my-wrapper__extra">
        <slot name="extra">{{extra}}</slot>
      </div>

      <div v-if="links && links.length" class="my-wrapper__links">
        <span v-for="(item, index) in links"
              :key="`link${index}`"
              class="my-wrapper__links-item"
              :class="{'is-active':item.value===activeLink}"
              @click="handleLinkClick(item)">
          <slot name="link" :link="item" :index="index">
               {{item.label}}
          </slot>
        </span>
      </div>
    </div>
    <div ref="body" class="my-wrapper__body">
      <slot></slot>
    </div>

    <div v-if="$slots.footer" class="my-wrapper__footer">
      <slot name="footer"></slot>
    </div>


  </div>
</template>

<script>
  
     import __vue__ from 'vue';
     
     import {MyBreadcrumb as __MyBreadcrumb__,MyFloat as __MyFloat__,MyFloatItem as __MyFloatItem__} from '$ui';
     
     
     
     /**
   * 
   * @module $ui/components/my-wrapper
   */
  import {MyBreadcrumb, MyFloat, MyFloatItem} from '$ui'
  import {on, off} from 'element-ui/lib/utils/dom'

  /**
   * 
   * @member slots
   * @property {string} default my-container
   * @property {string} title 
   * @property {string} subTitle 
   * @property {string} actions 
   * @property {string} extra 
   * @property {string} link link indexlinks
   * @property {string} footer 
   */

  export default {
    name: 'MyWrapper',
    components: {
      MyBreadcrumb,
      MyFloat,
      MyFloatItem
    },
    /**
     * 
     * @member props
     * @property {string} [title] 
     * @property {string} [subTitle]  
     * @property {string} [extra] 
     * @property {Array} [breadcrumb] ,label, icon, to
     * @property {string} breadcrumb.label 
     * @property {string|object} [breadcrumb.icon] 
     * @property {string|object} [breadcrumb.to] 
     * @property {Array} [links] label, value, to
     * @property {string} [links.label] 
     * @property {string|number} [links.value] 
     * @property {string|object} [links.to] 
     * @property {string|number} [defaultActiveLink] 
     * @property {boolean} [header=true] 
     * @property {boolean} [split=true] 
     * @property {boolean} [back=false] 
     * @property {boolean} [fit=false] 
     */
    props: {
      title: String,
      subTitle: String,
      extra: String,
      //  [{label, icon, to}]
      breadcrumb: Array,
      // [{label, value, to}]
      links: Array,
      defaultActiveLink: [String, Number],
      header: {
        type: Boolean,
        default: true
      },
      // 
      fit: {
        type: Boolean,
        default: false
      },
      // 
      split: {
        type: Boolean,
        default: true
      },
      // 
      back: Boolean
    },
    data() {
      return {
        activeLink: this.defaultActiveLink,
        showHeaderLine: false
      }
    },
    computed: {
      classes() {
        return {
          'has-links': this.links && this.links.length > 0,
          'is-border-line': this.showHeaderLine
        }
      }
    },
    watch: {
      defaultActiveLink: {
        immediate: true,
        handler(val) {
          this.activeLink = val
        }
      }
    },
    methods: {
      handleLinkClick(item) {
        this.activeLink = item.value
        if (this.$router && item.to) {
          this.$router.push(item.to)
        }
        /**
         * 
         * @event link-click
         * @param {Object} item 
         */
        this.$emit('link-click', item)
      },
      handleBack() {
        if (this.$listeners.back) {
          /**
           * 
           * @event back
           */
          this.$emit('back')
        } else {
          this.$router && this.$router.back()
        }
      },
      handleScroll(e) {
        const scrollTop = e.target.scrollTop
        this.showHeaderLine = scrollTop > 20
      }
    },
    mounted() {
      this.$refs.body && on(this.$refs.body, 'scroll', this.handleScroll)
    },
    beforeDestroy() {
      this.$refs.body && off(this.$refs.body, 'scroll', this.handleScroll)
    }
  }
     
     __vue__.use(__MyBreadcrumb__);
__vue__.use(__MyFloat__);
__vue__.use(__MyFloatItem__)
    
  </script>


  