
  <template>
  <div class="my-sidebar" :class="classes">
    <!--  -->
    <div class="my-sidebar__brand" v-if="logo || title">
      <a v-if="collapsed" :href="href">
        <slot name="brand" :title="title" :logo="logo">
          <img class="my-sidebar__logo" v-if="logo" :src="logo" :alt="title">
        </slot>
      </a>
      <a v-else :href="href">
        <slot name="brand" :title="title" :logo="logo">
          <img class="my-sidebar__logo" v-if="logo" :src="logo" :alt="title">
          <h1 class="my-sidebar__title" v-if="title">{{title}}</h1>
        </slot>
        <span v-if="version" class="my-sidebar__version">{{version}}</span>
      </a>
    </div>
    <div v-if="collapsible" @click="toggleCollapse" class="my-sidebar__trigger">
      <my-icon :name="triggerIcon" svg></my-icon>
    </div>
    <div class="my-sidebar__body" :class="bodyClasses">
      <!--  -->
      <slot></slot>

      <!--  -->
      <my-menu v-bind="menuOptions" :data="menus" @select="handleSelect">
        <template v-if="$scopedSlots['menu-item']" v-slot:title="{item}">
          <slot name="menu-item" :item="item"></slot>
        </template>
      </my-menu>


      <!--  -->
      <slot name="append"></slot>

    </div>
  </div>
</template>

<script>
  
     import __vue__ from 'vue';
     
     import {MyIcon as __MyIcon__,MyMenu as __MyMenu__} from '$ui';
     
     
     
     /**
   * 
   * @module $ui/components/my-sidebar
   */

  import {MyMenu, MyIcon} from '$ui'
  import defaultLogo from '$ui/assets/logo.png'
  import '$ui/icons/indent'
  import '$ui/icons/outdent'

  /**
   * 
   * @member slots
   * @property {string} default 
   * @property {string} brand logo
   * @property {string} menu-item   item 
   * @property {string} append 
   */
  export default {
    name: 'MySidebar',
    components: {
      MyMenu,
      MyIcon
    },
    /**
     * 
     * @member props
     * @property {string} [logo] logo
     * @property {string} [title] 
     * @property {string} [version] 
     * @property {string} [href] 
     * @property {boolean} [collapsible=false] 
     * @property {boolean} [collapsed=false] collapsible truesync
     * @property {array} [menus] ,  my-menu 
     * @property {object} [menuProps]  my-menu 
     * @property {string} [theme=light] 'light', 'dark', 'primary', 'gradual'
     * @property {boolean} [shadow] 
     */
    props: {
      // logo 
      logo: {
        type: String,
        default: defaultLogo
      },
      // 
      title: String,

      // 
      version: String,
      // 
      href: {
        type: String
      },
      // 
      collapsible: Boolean,

      // , sync
      collapsed: Boolean,

      // 
      menus: Array,

      // 
      menuProps: Object,

      // 
      theme: {
        type: String,
        default: 'light',
        validator(val) {
          return ['light', 'dark', 'primary', 'gradual', 'black'].includes(val)
        }
      },
      // 
      shadow: Boolean
    },
    computed: {
      classes() {
        return {
          [`is-${this.theme}`]: !!this.theme,
          'is-collapsed': this.collapsed,
          'is-shadow': this.shadow
        }
      },
      bodyClasses() {
        return {
          'has-brand': this.logo || this.title,
          'has-trigger': this.collapsible
        }
      },
      menuOptions() {
        return {
          ...(this.menuProps || {}),
          mode: 'vertical',
          theme: this.theme,
          collapsed: this.collapsed
        }
      },
      triggerIcon() {
        return this.collapsed
          ? 'icon-indent'
          : 'icon-outdent'
      }
    },
    methods: {
      handleSelect(item) {
        /**
         * 
         * @event select
         * @param {Object} item 
         */
        this.$emit('select', item)
      },
      toggleCollapse() {
        this.$emit('update:collapsed', !this.collapsed)
      }
    }
  }
     
     __vue__.use(__MyIcon__);
__vue__.use(__MyMenu__)
    
  </script>


  