<script>
  /**
   * 
   * @module $ui/components/my-menu
   */
  import {MyIcon} from '$ui'
  import {Menu, Submenu, MenuItem, MenuItemGroup, Tooltip, Badge} from 'element-ui'
  import renderers from './renderers'
  import {isUrl} from '$ui/utils/regex'
  import {addResizeListener, removeResizeListener} from 'element-ui/lib/utils/resize-event'

  /**
   * 
   * @member slots
   * @property {string} title   item 
   */
  export default {
    name: 'MyMenu',
    mixins: [renderers],
    components: {
      Menu,
      Submenu,
      MenuItem,
      MenuItemGroup,
      Tooltip,
      Badge,
      MyIcon
    },
    /**
     * 
     * @member props
     * @property {Item[]} [data] 
     * @property {string} [data.icon] className
     * @property {string} [data.text] 
     * @property {number|object} [data.badge] 
     * @property {string} [data.index] path
     * @property {string} [data.trigger] route href blank event 
     * @property {boolean} [data.disabled] 
     * @property {Item[]} [data.children] 
     * @property {boolean} [data.group] 
     * @property {string} [data.title] 
     * @property {Number} [itemWidth=150]  horizontal 
     * @property {string} [mode=vertical] 'vertical', 'horizontal'
     * @property {string} [theme=light]  , 'light', 'dark', 'primary'
     * @property {boolean} [router=false]  vue-router  index  path 
     * @property {string} [defaultActive]  index, routertruepath
     * @property {String[]} [defaultOpeneds]  sub-menu  index 
     * @property {boolean} [uniqueOpened] 
     * @property {boolean} [collapsed=false]  mode  vertical 
     * @property {string} [menuTrigger=hover] ( mode  horizontal ) 'hover', 'click'
     * @property {boolean} [collapseTransition=true] 
     * @property {object} [submenu]  <a href="https://element.eleme.io/#/zh-CN/component/menu#submenu-attribute"> ElementUI SubMenu Attribute </a>
     */
    props: {
      // 
      data: Array,
      //  horizontal 
      itemWidth: {
        type: Number,
        default: 175
      },
      mode: {
        type: String,
        default: 'vertical',
        validator(val) {
          return ['vertical', 'horizontal'].includes(val)
        }
      },
      theme: {
        type: String,
        default: 'light',
        validator(val) {
          // gradual
          return ['light', 'dark', 'primary', 'gradual', 'black'].includes(val)
        }
      },
      router: Boolean,
      defaultActive: String,
      defaultOpeneds: Array,
      uniqueOpened: Boolean,
      collapsed: Boolean,
      menuTrigger: {
        type: String,
        default: 'hover',
        validator(val) {
          return ['hover', 'click'].includes(val)
        }
      },
      collapseTransition: {
        type: Boolean,
        default: true
      },
      submenu: {
        type: Object,
        default() {
          return {
            popperClass: 'my-menu--popup',
            popperAppendToBody: true
          }
        }
      }
    },
    data() {
      return {
        viewWidth: 0,
        active: true
      }
    },
    computed: {
      menuData() {
        const data = this.data || []
        if (this.mode === 'vertical') {
          return data
        }
        if (this.viewWidth === 0) {
          return []
        }
        const moreWidth = 80
        const diff = this.viewWidth - moreWidth
        if (diff <= 0) {
          return []
        }
        const count = Math.floor(diff / this.itemWidth)

        const showItems = data.slice(0, count)
        const otherItems = data.slice(count)
        if (otherItems.length > 0) {
          const more = {
            text: '...',
            index: 'more',
            children: otherItems
          }
          showItems.push(more)
        }
        return showItems
      },
      menuProps() {
        let defaultActive = this.defaultActive
        // defaultActiverouter
        if (!defaultActive && this.router && this.$route) {
          defaultActive = this.$route.path
        }
        return {
          mode: this.mode,
          defaultActive: defaultActive,
          defaultOpeneds: this.defaultOpeneds,
          uniqueOpened: this.uniqueOpened,
          collapse: this.mode === 'vertical' && this.collapsed,
          menuTrigger: this.menuTrigger,
          collapseTransition: this.collapseTransition
        }
      },
      submenuProps() {
        return {
          ...this.submenu,
          popperClass: [this.submenu.popperClass, `is-${this.theme}`].join(' ')
        }
      },
      classes() {
        return {
          'my-menu': true,
          [`is-${this.theme}`]: !!this.theme
        }
      }
    },
    watch: {
      mode() {
        // el-menu bugDOMhackel-menu
        this.active = false
        this.$nextTick(() => {
          this.active = true
        })
      }
    },
    methods: {
      findNode(index) {
        const findItem = function (nodes = [], index) {
          for (let i = 0, len = nodes.length; i < len; i++) {
            const node = nodes[i]
            if (node.index === index) {
              return node
            }
            if (node.children) {
              const result = findItem(node.children, index)
              if (result) {
                return result
              }
            }
          }
        }
        return findItem(this.menuData, index)
      },
      handleSelect(index) {
        const item = this.findNode(index)
        if (!item) return

        if (isUrl(item.index)) {
          if (typeof window === 'undefined') return
          if (item.trigger === 'blank') {
            window.open(item.index)
          } else {
            window.location.href = item.index
          }
          return
        }

        if (item.trigger) {
          switch (item.trigger) {
            case 'route':
              this.$router.push(item.index).catch(e => e)
              break
            case 'href':
              if (typeof window === 'undefined') return
              window.location.href = item.index
              break
            case 'blank':
              if (typeof window === 'undefined') return
              window.open(item.index)
              break
          }
        } else if (this.router && this.$router) {
          this.$router.push(item.index).catch(e => e)
        }

        /**
         * 
         * @event select
         * @param {Object} item 
         */
        this.$emit('select', item)

      },
      handleResize() {
        this.viewWidth = this.$el.getBoundingClientRect().width
      }
    },
    render() {
      return (
        <div class="my-menu__wrapper">
          {
            this.active
              ? (
                <Menu {...{props: this.menuProps}} class={this.classes} onSelect={this.handleSelect}>
                  {
                    this.menuData.map(item => this.itemRender(item, true))
                  }
                </Menu>
              )
              : null
          }
        </div>
      )

    },
    mounted() {
      this.handleResize()
      addResizeListener(this.$el, this.handleResize)
    },
    beforeDestroy() {
      removeResizeListener(this.$el, this.handleResize)
    }
  }
</script>

