
  <template>
  <my-layout :class="classes" :direction="direction" :west="west" :fit="fixed" :north="north">
    <template v-if="header" v-slot:north>
      <my-navbar v-bind="navbarOptions"
                 @select="handleMenuSelect"
                 :collapsed.sync="collapsed">
        <Breadcrumb v-if="breadcrumbVisible && breadcrumbList && breadcrumbList.length"
                    :class="`my-pro__breadcrumb is-${navbarOptions.theme}`"
                    :data="breadcrumbList"></Breadcrumb>
        <slot v-if="$slots.navbar" name="navbar"></slot>
        <template v-slot:actions>
          <slot name="actions"></slot>
        </template>
      </my-navbar>
    </template>

    <template v-if="sidebarOptions" v-slot:west>
      <my-sidebar v-bind="sidebarOptions"
                  v-clickoutside="handleClickOutside"
                  :menus="menus"
                  :menuProps="menuProps"
                  @select="handleMenuSelect"
                  :collapsed.sync="collapsed">
        <slot name="sidebar"></slot>
        <template v-slot:append>
          <slot name="append"></slot>
        </template>
      </my-sidebar>
    </template>

    <Tabs v-if="tab && tabs.length"
          :data="tabs"
          :active="activeTabValue"
          @select="handleTabSelect"
          @command="handleTabCommand"
          @remove="handleTabRemove"></Tabs>

    <div class="my-pro__main" :class="mainClasses">
      <slot></slot>
    </div>

  </my-layout>
</template>

<script>
  
     import __vue__ from 'vue';
     
     import {MyLayout as __MyLayout__,MyNavbar as __MyNavbar__,MySidebar as __MySidebar__} from '$ui';
     
     
     
     /**
   * 
   * @module $ui/components/my-pro
   */
  import {MyLayout, MyNavbar, MySidebar} from '$ui'
  import Breadcrumb from './Breadcrumb'
  import Tabs from './Tabs'
  import clickoutside from 'element-ui/lib/utils/clickoutside'
  import responsive from '$ui/utils/responsive'
  import defaultLogo from '$ui/assets/logo.png'

  const {IconAction, UserAction, FullScreenAction, Action} = MyNavbar

  /**
   * 
   * @member slots
   * @property {string} default  router-view
   * @property {string} navbar 
   * @property {string} actions IconAction, UserAction, FullScreenAction
   * @property {string} sidebar 
   * @property {string} append 
   *
   */
  export default {
    IconAction,
    UserAction,
    FullScreenAction,
    Action,
    name: 'MyPro',
    components: {
      MyLayout,
      MyNavbar,
      MySidebar,
      Breadcrumb,
      Tabs
    },
    directives: {
      clickoutside
    },
    provide() {
      return {
        myPro: this
      }
    },
    /**
     * 
     * @member props
     * @property {string} [logo] logo
     * @property {string} [title=MyWeb] 
     * @property {string} [version] 
     * @property {string} [href] logo
     * @property {Array} [menus] MyMenu
     * @property {string} [menus.icon] className
     * @property {string} [menus.text] 
     * @property {number|object} [menus.badge] 
     * @property {string} [menus.index] path
     * @property {string} [menus.trigger] route href blank event 
     * @property {boolean} [menus.disabled] 
     * @property {Item[]} [menus.children] 
     * @property {boolean} [menus.group] 
     * @property {string} [menus.title] 
     * @property {Object} [menuProps] MyMenu
     * @property {string} [mode=sidebar] : sidebar()navbar()both(+)
     * @property {string} [sidebarTheme=light] 'light', 'dark', 'primary', 'gradual'
     * @property {string} [navbarTheme=light] 'light', 'dark', 'primary', 'gradual'
     * @property {boolean} [fixed=false] 
     * @property {boolean} [colorWeak=false] 
     * @property {boolean} [collapsible=true] 
     * @property {boolean} [header=true] 
     * @property {boolean} [rainbow=true] 
     * @property {Object|Function} [breadcrumb] path
     * @property {string|Function} [documentTitle] metatitle
     * @property {Object|Function} [tab] metatab{label, value, icon}
     * @property {string} tab.label 
     * @property {string} tab.value path
     * @property {string|object} [tab.icon] 
     */
    props: {
      logo: {
        type: String,
        default: defaultLogo
      },
      title: {
        type: String,
        default: 'MyWeb'
      },
      version: {
        type: String
      },
      href: String,
      menus: {
        type: Array
      },
      menuProps: Object,
      // : sidebar()navbar()both(+)
      mode: {
        type: String,
        default: 'sidebar',
        validator(val) {
          return ['sidebar', 'navbar', 'both'].includes(val)
        }
      },
      sidebarTheme: String,
      navbarTheme: String,
      // 
      fixed: Boolean,
      // 
      colorWeak: Boolean,
      // 
      collapsible: {
        type: Boolean,
        default: true
      },
      // 
      header: {
        type: Boolean,
        default: true
      },
      // 
      rainbow: {
        type: Boolean,
        default: true
      },
      breadcrumb: [Object, Function],
      // 
      documentTitle: [String, Function],
      // tabtabs
      tab: [Object, Function]
    },
    data() {
      return {
        collapsed: false,
        screen: {},
        breadcrumbList: [],
        tabs: [],
        activeTabValue: ''
      }
    },
    computed: {
      classes() {
        const screen = {}
        Object.keys(this.screen).forEach(n => {
          screen[`is-${n}`] = this.screen[n]
        })
        return {
          'my-pro': true,
          'is-color-weak': this.colorWeak,
          'is-collapsed': this.collapsed,
          [`my-pro--${this.mode}`]: true,
          'is-rainbow': this.rainbow,
          [`my-pro--${this.sidebarTheme}`]: !!this.sidebarTheme,
          'is-fixed': this.fixed,
          ...screen
        }
      },
      direction() {
        return this.mode === 'sidebar' ? 'horizontal' : 'vertical'
      },
      west() {
        const {xs} = this.screen
        return {
          width: this.collapsed ? (xs ? 0 : 65) : 256
        }
      },
      north() {
        return {
          height: ((this.mode === 'both' && this.rainbow) ||
            this.mode === 'sidebar' ||
            (this.mode === 'navbar' && this.rainbow)) ? 61 : 60
        }
      },
      navbarOptions() {
        let opt = null
        const {xs} = this.screen
        switch (this.mode) {
          case 'sidebar':
            opt = {
              title: null,
              logo: null,
              collapsible: this.collapsible,
              theme: this.navbarTheme,
              shadow: false
            }
            break
          case 'navbar':
            opt = {
              title: xs ? null : this.title,
              logo: this.logo,
              version: xs ? null : this.version,
              href: this.href,
              collapsible: false,
              theme: this.navbarTheme,
              menus: xs ? null : this.menus,
              menuProps: this.menuProps,
              shadow: true
            }
            break
          case 'both':
            opt = {
              title: xs ? null : this.title,
              logo: this.logo,
              version: xs ? null : this.version,
              href: this.href,
              collapsible: !!xs,
              theme: this.navbarTheme,
              shadow: true
            }
            break
        }
        return opt
      },
      sidebarOptions() {
        let opt = null
        // const {xs} = this.screen
        switch (this.mode) {
          case 'sidebar':
            opt = {
              title: this.title,
              logo: this.logo,
              href: this.href,
              version: this.version,
              theme: this.sidebarTheme,
              collapsible: false,
              shadow: true
            }
            break
          case 'both':
            opt = {
              title: null,
              logo: null,
              version: null,
              collapsible: this.collapsible,
              theme: this.sidebarTheme,
              shadow: false
            }
            break
        }
        return opt
      },
      breadcrumbVisible() {
        if (!this.breadcrumb) return false
        const {xs} = this.screen
        return !xs
      },
      mainClasses() {
        return {
          'is-fixed': this.fixed,
          'has-tabs': !!this.tab
        }
      }

    },
    watch: {
      collapsed(val) {
        /**
         * 
         * @event collapse
         * @param {boolean} collapsed 
         */
        this.$emit('collapse', val)
      },
      $route: {
        immediate: true,
        handler() {
          const matched = this.$route.matched.slice(0).pop()
          this.$nextTick(() => {
            this.updateBreadcrumbList(matched)
            const tab = this.collectTab(this.$route.fullPath, matched)
            tab && this.addTab(tab)
          })
          this.updateDocumentTitle(matched)
        }
      },
      tab: {
        immediate: true,
        handler() {
          this.addDefaultTab()
        }
      }
    },
    methods: {
      handleMenuSelect(item) {
        /**
         * 
         * @event select
         * @param {Object} item 
         */
        this.$emit('select', item)
      },
      updateScreen(screen) {
        this.screen = screen
        if (this.mode === 'sidebar') {
          if (this.screen.sm && !this.collapsed) {
            this.collapsed = true
          }
          if (this.screen.lg && this.collapsed) {
            this.collapsed = false
          }
        }

        if (this.mode === 'both') {
          if (this.screen.sm && !this.collapsed) {
            this.collapsed = true
          }
          if (this.screen.lg && this.collapsed) {
            this.collapsed = false
          }
        }


      },
      handleClickOutside() {
        if (this.mode === 'sidebar' && this.screen.xs && !this.collapsed) {
          this.collapsed = true
        }
      },
      updateBreadcrumbList(matched) {
        if (!this.breadcrumb) return
        if (!matched) {
          this.breadcrumbList = null
          return
        }
        if (typeof this.breadcrumb === 'function') {
          this.breadcrumbList = this.breadcrumb(matched)
        } else {
          this.breadcrumbList = this.breadcrumb[matched.path] || []
        }
      },
      updateDocumentTitle(matched) {
        if (!this.documentTitle) return

        document.title = typeof this.documentTitle === 'function'
          ? this.documentTitle(matched)
          : ((matched.meta || {}).title || this.documentTitle)
      },
      addDefaultTab() {
        if (!this.tab) return
        const isFunction = typeof this.tab === 'function'
        if (isFunction) {
          const tab = this.tab()
          tab && this.addTab({
            ...tab,
            closable: false
          })
        } else {
          this.addTab({
            ...this.tab,
            closable: false
          })
        }

      },
      collectTab(fullPath, matched) {
        if (!this.tab) return null
        const isFunction = typeof this.tab === 'function'
        if (isFunction) {
          const tab = this.tab(fullPath, matched)
          if (!tab) return null
          return {
            closable: true,
            ...tab
          }
        } else {
          const isDefault = fullPath === this.tab.value
          const {tab, icon} = matched.meta || {}
          if (!tab) return null
          return {
            label: tab,
            value: fullPath,
            icon: icon,
            path: matched.path,
            closable: true,
            ...(isDefault ? {
              ...this.tab,
              closable: false
            } : null)
          }
        }


      },
      /**
       * 
       * @method addTab
       * @param {Object} tab 
       * @param {string} tab.label 
       * @param {string} tab.value path
       * @param {string|object} [tab.icon] 
       * @param {boolean} [tab.closable] 
       * @param {string} [tab.path] path
       */
      addTab(tab) {
        if (!tab) return
        tab.path = tab.path || tab.value
        const index = this.tabs.findIndex(n => n.value === tab.value || n.path === tab.path)
        if (index > -1) {
          this.tabs.splice(index, 1, tab)
        } else {
          this.tabs.push(tab)
        }
        this.activeTabValue = tab.value

      },
      /**
       * 
       * @method setTab
       * @param {object} tab  {label,value,closable,path,icon}
       */
      setTab(tab) {
        this.$nextTick(() => {
          const fullPath = this.$route.fullPath
          const tabIndex = this.tabs.findIndex(t => t.value === fullPath)
          if (tabIndex > -1) {
            const newTab = {
              ...this.tabs[tabIndex],
              ...tab
            }
            this.tabs.splice(tabIndex, 1, newTab)
          }
        })
      },
      /**
       * 
       * @method closeTabs
       * @param {string|Function} value value  path,return true 
       */
      closeTabs(value) {
        const filter = typeof value === 'function'
          ? value
          : tab => tab.path === value || tab.value === value
        this.tabs = this.tabs.filter((tab, index) => !filter(tab, index))
      },
      handleTabSelect(tab, index) {
        if (tab.value) {
          this.$router.push(tab.value)
        }
        /**
         * 
         * @event tab-select
         * @param {object} tab 
         * @param {number} index 
         */
        this.$emit('tab-select', tab, index)
      },
      handleTabRemove(tab, index) {
        this.tabs.splice(index, 1)
        // tab
        if (tab.value === this.activeTabValue) {
          const current = this.tabs[index] || this.tabs[this.tabs.length - 1]
          current && this.$router.push(current.value)
        }
        /**
         * 
         * @event tab-remove
         * @param {object} tab 
         * @param {number} index 
         */
        this.$emit('tab-remove', tab, index)
      },
      handleTabCommand(command, tab, index) {
        switch (command) {
          case 'left':
            this.tabs.splice(1, index - 1)
            break
          case 'right':
            this.tabs.splice(index + 1, this.tabs.length)
            break
          case 'other':
            if (index === 0) {
              this.tabs.splice(1)
            } else {
              this.tabs = [this.tabs[0], this.tabs[index]]
            }
            break
          case 'all':
            this.tabs.splice(1)
            if (this.tabs[0]) {
              this.$router.push(this.tabs[0].value)
            }
            break
        }
      }
    },
    created() {
      this.token = responsive.on(e => {
        this.updateScreen(e)
      })


    },
    updated() {
      this.$nextTick(() => {
        const matched = this.$route.matched.slice(0).pop()
        this.updateBreadcrumbList(matched)
      })
    },
    beforeDestroy() {
      this.token && responsive.off(this.token)
    }
  }
     
     __vue__.use(__MyLayout__);
__vue__.use(__MyNavbar__);
__vue__.use(__MySidebar__)
    
  </script>


  