<script>
  /**
   * 4
   * @module $ui/components/my-layout
   */
  import {Container, Header, Main, Footer, Aside} from 'element-ui'

  const Components = {
    north: Header,
    default: Main,
    south: Footer,
    east: Aside,
    west: Aside
  }

  /**
   * 
   * @member slots
   * @property {string} default 
   * @property {string} north 
   * @property {string} south 
   * @property {string} west 
   * @property {string} east 
   */
  export default {
    name: 'MyLayout',
    components: {
      Container,
      Header,
      Main,
      Footer,
      Aside
    },
    /**
     * 
     * @member props
     * @property {boolean} [fit] 
     * @property {boolean} [border] 
     * @property {boolean} [split] 
     * @property {string} [direction] 'vertical', 'horizontal'
     * @property {Object} [north] 
     * @property {string|number} [north.height=60] 
     * @property {Object} [south] 
     * @property {string|number} [south.height=60] 
     * @property {Object} [west] 
     * @property {string|number} [west.width=300] 
     * @property {Object} [east] 
     * @property {string|number} [east.width=300] 
     */
    props: {
      // 
      fit: Boolean,
      // 
      border: Boolean,

      // 
      split: Boolean,

      // 
      direction: {
        type: String,
        validator(val) {
          return ['vertical', 'horizontal'].includes(val)
        }
      },
      // {size(width/height)}
      north: Object,
      south: Object,
      east: Object,
      west: Object
    },
    data() {
      return {}
    },
    computed: {
      classes() {
        return {
          'my-layout': true,
          'is-fit': this.fit,
          'is-border': this.border,
          'is-split': this.split,
          [`is-${this.direction}`]: !!this.direction
        }
      }
    },
    methods: {
      getDirection() {
        if (this.direction) return this.direction

        const {north, south, west, east} = this.$slots
        const isVertical = !west && !east
        const isHorizontal = !north && !south
        return (isVertical || (!isVertical && !isHorizontal)) ? 'vertical' : 'horizontal'
      },
      getRegionOptions(type) {
        return {
          ...(this[type] || {})
        }
      },
      regionRender(type) {
        if (!this.$slots[type]) return

        const options = this.getRegionOptions(type)
        const attrs = {
          north: 'height',
          south: 'height',
          west: 'width',
          east: 'width'
        }
        const defaultValues = {
          north: 60,
          south: 60,
          west: 256,
          east: 256
        }
        const name = attrs[type]
        const size = Number.parseInt(options[name] === undefined ? defaultValues[type] : options[name])

        return this.$createElement(Components[type], {
          class: [
            'my-layout__region',
            `my-layout__${type}`,
            {
              'is-split': this.split,
              'is-border': this.border
            }
          ],
          style: {
            [name]: typeof size === 'number' ? `${size}px` : size
          }
        }, (this.fit && size) ? [<div class="my-layout__fit">{this.$slots[type]}</div>] : this.$slots[type])
      }
    },
    render() {
      const {north, south, west, east} = this.$slots
      const direction = this.getDirection()

      const wrapHorizontal = () => {
        if (west || east) {
          return (
            <Container class="my-layout__wrapper" direction="horizontal">
              {
                [
                  this.regionRender('west'),
                  this.regionRender('default'),
                  this.regionRender('east')
                ]
              }
            </Container>
          )
        } else {
          return this.regionRender('default')
        }
      }

      const wrapVertical = () => {
        if (north || south) {
          return (
            <Container class="my-layout__wrapper" direction="vertical">
              {
                [
                  this.regionRender('north'),
                  this.regionRender('default'),
                  this.regionRender('south')
                ]
              }
            </Container>
          )
        } else {
          return this.regionRender('default')
        }
      }

      return (
        <Container direction={direction} class={this.classes}>

          {
            direction === 'vertical'
              ? [
                this.regionRender('north'),
                wrapHorizontal(),
                this.regionRender('south')
              ]
              : [
                this.regionRender('west'),
                wrapVertical(),
                this.regionRender('east')
              ]
          }

        </Container>
      )
    }
  }
</script>

